document.addEventListener('DOMContentLoaded', function() { //DOMContentLoaded, espera a que todo el sitio este cargado 
    crearGaleria(); 
});

//Creación de función
function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes'); //Llama a la class de HTML

    for( let i = 1; i <= 12; i++) { //Número de imagenes a generar
        const imagen = document.createElement('IMG'); //Crea la etiqueta imagen en HTML
        imagen.src = `build/img/thumb/${i}.webp`; //Manda a llamar la imagen y {i} define la posición de la imagen
        imagen.dataset.imagenId = i; //Agrega al codigo de la imagen un Data ID  (es un atributo personalizado)


        //Añade la función mostrarImagen
        imagen.onclick = mostrarImagen; //onclick agrega un evento 

        const lista = document.createElement('LI'); //Crea la etiqueta <li></li>
        lista.appendChild(imagen) //Agrega la imagen al <li></li>

        galeria.appendChild(lista); //Agrega el <li></li> con la imagen a la class en HTML y Finaliza el proceso
    }
}

function mostrarImagen(e) {
    const id = parseInt(e.target.dataset.imagenId) //Convierte string a numero (el string debe ser un numero ) Se vera en color azul

    //Genera la imagen de tamaño grande
    const imagen = document.createElement('IMG');
    imagen.src = `build/img/grande/${id}.webp`;
    imagen.classList.add('imagen');

    //Overlay agrega la imagen encima de todo 
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay'); //Agrega una class al DIV

    //Cerrar imagen con clic en cualquier lado (overlay)
    overlay.onclick = function() {
        overlay.remove();
        body.classList.remove('fijar-body')
    }

    //Boton para cerrar la imagen
    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent = 'X';
    cerrarImagen.classList.add('btn-cerrar');
    
    overlay.appendChild(cerrarImagen);

    // Cerrar Imagen al presionar X
    cerrarImagen.onclick = function() {
        overlay.remove();
        body.classList.remove('fijar-body')
    }

    //Mostrar en el html directamente en el body
    const body = document.querySelector('body'); //Selecciona la etiqueta
    body.appendChild(overlay);
    body.classList.add('fijar-body');
}