document.addEventListener('DOMContentLoaded', function() {
    scrollNav();

    navegacionFija();
});

function navegacionFija() {

    //Selecciona la Barra de Navegacion
    const barra = document.querySelector('.header');

    //registrar el IntersectionObserver
    const observer = new IntersectionObserver( function(entries) { //entries Nos da la informacion del elemtneo a Observar 
        if(entries[0].isIntersecting) {
            barra.classList.remove('fijo')
        } else {
            barra.classList.add('fijo')
        }
        
    });   

    //Elemento a observar 
    observer.observe(document.querySelector('.sobre-festival'));
}

function scrollNav() {
    const enlaces = document.querySelectorAll('.navegacion-principal a')

    enlaces.forEach( function( enlace ) { //para usar varios objetos no se puede usar un addEventListener al inicio 
        enlace.addEventListener('click', function(e) {
            e.preventDefault();
            const seccion = document.querySelector(e.target.attributes.href.value);

            seccion.scrollIntoView({ //Aplica el scroll normal y se agrega la funcion behavior
                behavior: 'smooth' //Scroll lento 
            });
        });
    });
}